import Vue from 'vue'
import { requestPayCashDesk, requestPayPreorder, sendSMSCode, requestPayBindCard, requestPayBindCardConfirm, requestPayBindPay, requestPayUnBindCard } from '@/api/API'
import BottomBar from '../../components/BottomBar'
import Header from '../../components/Header'
import TextUtil from '../../utils/TextUtil'
import { Field, Radio, MessageBox } from 'mint-ui'
Vue.component(Field.name, Field)
Vue.component(Radio.name, Radio)
let timer = null
export default {
  name: 'recharge',
  components: {
    Header, BottomBar
  },
  data () {
    return {
      // isOther: false,
      otherUrl: null,
      money: null,
      radio: null,
      radioOptions: [],
      cashType: undefined,
      is_special: 0,
      cashRadio: undefined,
      cashRadioOptions: [],
      max: null,
      min: null,
      rate: null,
      isShowBind: false,
      clickBindBtn: false,
      bind: {
        name: '',
        phone: '',
        id_card: '',
        validCode: '',
        bank_card: '',
        sendCodeCD: 0
      }
    }
  },
  mounted () {
    let user = this.$store.state.User.user || {}
    this.bind.phone = user.mobile || ''
    this.bind.name = user.real_name || ''
    this.getPayCashDesk()
  },
  watch: {
    radio (val, oldVal) {
      this.cashRadio = undefined
      this.initCashRadioOption(val)
      // if (this.cashType === '2' && this.cashRadioOptions.length === 0) {
      //   this.isShowBind = true
      // }
    }
  },
  methods: {
    getTips () {
      let tips = ''
      if (this.rate !== null) {
        tips += '(提示：手续费' + this.rate + '%'
      }
      if (this.max !== null || this.min !== null) {
        if (tips === '') {
          tips += '(提示：入金'
        } else {
          tips += '，入金'
        }
        if (this.max !== null) {
          tips += '上限' + this.max + '元'
        }
        if (this.max !== null && this.min !== null) {
          tips += '/'
        }
        if (this.min !== null) {
          tips += '下限' + this.min + '元'
        }
      }
      if (tips !== '') {
        tips += ')'
      }
      return tips
    },
    async getPayCashDesk () {
      let user = this.$store.state.User || {}
      let res = await requestPayCashDesk(user.token)
      if (res.code === 200) {
        let options = []
        for (let key in res.data.content) {
          let arr = res.data.content[key]
          arr.forEach(obj => {
            options.push(Object.assign({label: obj.name, value: obj.id}, obj))
          })
        }
        this.radioOptions = options
        this.initCashRadioOption(this.radio)
      }
    },
    initCashRadioOption (val) {
      this.cashType = undefined
      this.is_special = 0
      if (!val) {
        this.cashRadioOptions = []
        return
      }
      let options = []
      this.radioOptions.forEach(obj1 => {
        if (obj1.id === val) {
          this.is_special = obj1.is_special
          this.max = obj1.max
          this.min = obj1.min
          this.rate = obj1.rate
          this.cashType = obj1.type
          if (obj1.type === '1') {
            obj1.banks.forEach(obj2 => {
              options.push(Object.assign({}, obj2))
            })
          } else if (obj1.type === '2' && obj1.binded.length > 0) {
            obj1.binded.forEach(obj2 => {
              options.push(Object.assign({}, obj2))
            })
          }
        }
      })
      this.cashRadioOptions = options
    },
    async onSendCodeClicked () {
      if (this.bind.phone.length < 1) {
        window.EventBus.$emit('globalMessage', '请输入手机号码')
        return
      }
      if (!TextUtil.isPhoneValid(this.bind.phone)) {
        window.EventBus.$emit('globalMessage', '请输入正确的手机号码')
        return
      }
      let res = await sendSMSCode(this.bind.phone, this.$store.state.User.uuid)
      if (res.code === 200) {
        window.EventBus.$emit('globalMessage', res.msg)
      }
      this.bind.sendCodeCD = 60
      this.startTimer()
    },
    startTimer () {
      timer = setInterval(() => {
        if (this.bind.sendCodeCD > 0) {
          this.bind.sendCodeCD--
        } else {
          clearInterval(timer)
        }
      }, 1000)
    },
    async onSubmit () {
      if (!this.money) {
        return window.EventBus.$emit('globalMessage', '请输入充值金额')
      }
      if (!this.radio) {
        return window.EventBus.$emit('globalMessage', '请选择支付通道')
      }
      if (this.cashType === '1' && !this.cashRadio) {
        return window.EventBus.$emit('globalMessage', '请选择银行')
      }
      if (this.cashType === '2' && !this.cashRadio) {
        if (this.cashRadioOptions.length === 0) {
          this.isShowBind = true
          return
        }
        return window.EventBus.$emit('globalMessage', '请选择卡号')
      }
      let bankId
      let cardId
      if (this.cashType === '1') {
        bankId = this.cashRadio
      } else if (this.cashType === '2') {
        cardId = this.cashRadio
      }
      let user = this.$store.state.User || {}
      let res = await requestPayPreorder(this.radio, bankId, cardId, this.money, user.token)
      if (res.code !== 200) {
        return window.EventBus.$emit('globalMessage', res.msg)
      }
      if (this.cashType === '2' && this.is_special !== 1) {
        MessageBox.prompt(null, '快捷方式需输入短信验证码', {inputValidator: v => !!v, inputErrorMessage: '短信验证码不能为空'}).then(({ value, action }) => {
          if (action === 'confirm') {
            this.sendBindSMSCode(value, res.data.content.url)
          }
        }).catch(error => {
          console.log(error)
        })
      } else {
        // window.location.href = res.data.content.url
        this.goThirdUrl(res.data.content.url)
      }
    },
    async onUnbind (id) {
      if (this.cashType !== '2' || this.is_special === 1) return
      let user = this.$store.state.User || {}
      let res = await requestPayUnBindCard(user.token, this.radio, id)
      if (res.code === 200) {
        this.cashRadio = null
        this.getPayCashDesk()
      }
      window.EventBus.$emit('globalMessage', res.msg)
    },
    onShowBind () {
      this.isShowBind = true
      this.clickBindBtn = true
    },
    onCancelShow () {
      this.isShowBind = false
      this.clickBindBtn = false
    },
    onExec () {
      if (this.is_special === 1 && !this.clickBindBtn) {
        return this.specialProPay()
      } else {
        return this.onBind()
      }
    },
    async specialProPay () {
      if (!this.bind.name) {
        return window.EventBus.$emit('globalMessage', '请输入姓名')
      }
      if (!this.bind.phone) {
        return window.EventBus.$emit('globalMessage', '请输入手机号')
      }
      if (!this.bind.id_card) {
        return window.EventBus.$emit('globalMessage', '请输入身份证号')
      }
      if (!this.bind.bank_card) {
        return window.EventBus.$emit('globalMessage', '请输入银行卡号')
      }
      let bankId
      let cardId
      if (this.cashType === '1') {
        bankId = this.cashRadio
      } else if (this.cashType === '2') {
        cardId = this.cashRadio
      }
      this.onCancelShow()
      let user = this.$store.state.User || {}
      let res = await requestPayPreorder(this.radio, bankId, cardId, this.money, user.token, this.bind.name, this.bind.phone, this.bind.id_card, this.bind.bank_card)
      if (res.code !== 200) {
        return window.EventBus.$emit('globalMessage', res.msg)
      }
      // window.location.href = res.data.content.url
      return this.goThirdUrl(res.data.content.url)
    },
    async onBind () {
      let user = this.$store.state.User || {}
      let res = await requestPayBindCard(user.token, this.radio, this.bind.name, this.bind.phone, this.bind.id_card, this.bind.bank_card)
      if (res.code === 200) {
        let orderId = res.data.content.order_id
        MessageBox.prompt(null, '快捷方式绑定短信验证', {inputValidator: v => !!v, inputErrorMessage: '短信验证码不能为空'}).then(({ value, action }) => {
          if (action === 'confirm') {
            this.bindConfirm(value, orderId)
          }
        }).catch(error => {
          console.log(error)
        })
      }
    },
    async bindConfirm (sms_code, order_id) {
      let user = this.$store.state.User || {}
      let res = await requestPayBindCardConfirm(user.token, this.radio, sms_code, order_id)
      if (res.code === 200) {
        this.onCancelShow()
        this.getPayCashDesk()
      }
      window.EventBus.$emit('globalMessage', res.msg)
    },
    async sendBindSMSCode (value, url) {
      let user = this.$store.state.User || {}
      let res = await requestPayBindPay(user.token, value, url)
      window.EventBus.$emit('globalMessage', res.msg)
    },
    goThirdUrl (url) {
      let u = navigator.userAgent
      //let app = navigator.appVersion
      //let isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1 //android终端或者uc浏览器
      let isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/) //ios终端
      if (window.android) {
        window.android.gopay(url)
      } else if (isiOS) {
        window.webkit.messageHandlers.gopay.postMessage(url)
      } else {
        window.location.href = url
      }
    }
  }
}



